.card {
    position: relative;
    width: 380px;
    height: 460px;
    background: linear-gradient(114.99deg, #471CA9 -0.99%, #5736A3 54.28%, #4B2A99 78.99%);
    box-shadow: -2.5777px 6.87386px 20.6216px rgba(0, 0, 0, 0.23);
    border-radius: 20px;
    padding-top: 286px;
    text-align: center;

}

.card::before,
.card::after {
        content: "";
        position: absolute;
    }

        .card::after {
        left: 20px;
        top: 20px;
        width: 76px;
        height: 22px;
        /* background-image: url(../../images/Logo.png); */
    }

        .card::before {
        position: absolute;
        display: block;
        left: 0;
        top: 50%;
        width: 100%;
        height: 10px;
        background: #EBD8FF;
        box-shadow: 0px 3.43693px 3.43693px rgba(0, 0, 0, 0.06), inset 0px -1.71846px 3.43693px #AE7BE3, inset 0px 3.43693px 2.5777px #FBF8FF;
    }


.avatarWrap {
    position: absolute;
    left: 50%;
    top: 50%;
    translate: (-50% -50%);
    width: 80px;
    height: 80px;
    padding: 10px;
    background-color: #ffd8d8;
    box-shadow: 0px 4.39163px 4.39163px rgba(0, 0, 0, 0.06), inset 0px -2.19582px 4.39163px #AE7BE3, inset 0px 4.39163px 3.29372px #FBF8FF;
    border-radius: 86px;
}

.avatarWrap::before {
    content: "";
    position: absolute;
    left: 36px;
    bottom: -15px;
    translate: (-50% -50%);
    width: 308px;
    height: 168px;
    background-image: url(../../images/bg-user.png);
}

.userImg {
    display: block;
    border-radius: 86px;

}

.info {
    text-align: center;
    list-style: none;
    margin-top: 20px;
    margin-bottom: 26px;
    padding: 0;

}

.text {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 1.2;
    text-transform: uppercase;
    color: #EBD8FF;
    margin: 0;

}

.text:first-child {
    margin-bottom: 10px;
}