.btn {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 22px;
    text-transform: uppercase;
    color: #373737;
    width: 196px;
    height: 50px;
    cursor: pointer;
    transition: background-color 200ms linear;

    box-shadow: 0px 3.43693px 3.43693px rgba(0, 0, 0, 0.25);
    border-radius: 10.3108px;

}

.btn:active {
        background: #5CD3A8;
    }

.btn:hover {
        background: #e0ff70;
    }